@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #fff;
    --color-bg-dark: #0f3c49;
    --color-blue: #11607d;
    --color-yellow: #f8bb4b;
    --color-white: #e7f1f2;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-size: 14px;
    background: var(--color-bg);
    color: var(--color-blue);
    line-height: 1.7;
}

/* --------GENERAL STYLES-------- */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-family: "Bebas Neue", "Arial Narrow";
    font-weight: 600;
    letter-spacing: 3px;
}

h1 {
    font-size: 120px;
    text-align: center;
    vertical-align: middle;
    line-height: 70vh;
    text-transform: uppercase;
}

h2 {
    font-size: 40px;
}

h3 {
    font-size: 25px;
}

section {
    margin-bottom: 8rem;
}

section>h2 {
    text-align: center;
    margin-bottom: 0 3rem;
}

a {
    font-family: "Bebas Neue", "Arial Narrow";
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 2.8px;
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    padding: 0.5rem 1.2rem;
    border-radius: 0.1rem;
    cursor: pointer;
    border: 1px solid var(--color-white);
    transition: var(--transition);
    font-size: 14px;
    font-family: "Bebas Neue", "Arial Narrow";
    font-weight: 500;
    letter-spacing: 1.6px;
}

.btn:hover {
    background: var(--color-yellow);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-blue);
    color: var(--color-white);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* --------MEDIA QUERIES FOR MED DEVICES (medium: tablets)-------- */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-bottom: 6rem;
    }
}

/* --------MEDIA QUERIES FOR SMALL DEVICES (small: mobile)-------- */

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section>h2 {
        margin-bottom: 2rem;
    }
}